<template>
  <div class="body-bg">
    <headerBar :header-data="headerData" />
    <div class="container-ca">
      <div class="login-cainfo">
        <div class="ca-icon"><van-image fit="cover" width="100%" height="100%" :src="imageList.img1" /></div>
        <div class="ca-text">CA移动证书</div>
      </div>

      <div class="login-form">
        <van-cell-group>
          <div class="input-item">
            <van-field
              ref="name"
              v-model="userAccount"
              clearable
              placeholder="请输入移动证书下载码"
            />
          </div>

          <div class="input-item password">
            <van-field
              ref="id"
              v-validate="'required|pinLength'"
              required
              clearable
              is-highlight
              is-title-latent
              type="number"
              placeholder="请输入原始移动证书PIN码"
              name="PIN"
              :error-message="errors.first('PIN')"
            />
            <!-- 显示与隐藏密码 -->
            <!-- <div v-if="eyesChange ==true" class="input-operator showpassword" @click="showEye()">
              <i class="iconfont iconkejian" />
            </div>
            <div v-else class="input-operator showpassword" @click="showEye()">
              <i class="iconfont iconbukejian" />
            </div> -->
          </div>

        </van-cell-group>
        <van-notice-bar
          style="margin-top:15px;"
          left-icon="info-o"
          text="此密码将作为您以后的移动证书登录密码,请妥善保管！"
        />
        <div class="login-button">
          <van-button round type="info" @click="goScan()">下载证书</van-button>
        </div>

      </div>
    </div>

    <!-- 证书安装成功弹窗 begin -->
    <van-popup v-model="show" closeable round style="width:90%;height:320px;">
      <div class="information-tips">
        <div class="tips-icon"><van-image fit="cover" width="100%" height="100%" :src="imageList.tipsimg1" /></div>
        <div class="information-tips-tit">CA移动证书下载成功</div>
        <div class="information-tips-txt">CA移动证书下载成功</div>

        <div class="information-tips-btn">
          <ul>
            <li><van-button plain type="info" size="large" round>返回首页</van-button></li>
            <li><van-button type="info" size="large" round>立即登录</van-button></li>
          </ul>
        </div>
      </div>
    </van-popup>

    <!-- 证书安装成功弹窗 end -->
  </div>

</template>

<script>
import { pageRouter, NativeCaJS, NativeJS } from '@/common/js/mixin.js'
import headerBar from '@/components/header/headerBar'
import { loadSearch } from '@/common/js/catch'
import { caSmqz, getSfxx } from '@/store/module.js'

export default {
  name: 'CaCz',
  components: {
    headerBar
  },

  mixins: [pageRouter, NativeCaJS, NativeJS],
  data() {
    return {
      userAccount: '',
      Native_SignMessage: '',
      headerData: {
        title: '扫码签章',
        leftUrl: 'back',
        leftText: '首页'
      },
      PIN: '', // 密码
      imageList: {
        img1: require('@/assets/images/login/icon-qianzhang.png'),
        tipsimg1: require('@/assets/images/icon-success.png'), /* 成功 */
        tipsimg2: require('@/assets/images/icon-info.png'), /* 警告 */
        tipsimg3: require('@/assets/images/icon-error.png') /* 失败 */
      },
      titleImage: true, // 标题图片
      eyesChange: true, // 是否展示密码
      show: false,
      Native_SmqzMessage: {}
    }
  },
  watch: {
  // 二维码数据
    Native_ScanData(val) {
      console.info('val===0==', val.name)
      if (val) {
        console.info('val===1==', val.name)
        // alert(val.name)
        this.token = val.name
        this.getScanData(val.name)
      }
    },

    // ca签名结果
    Native_SmqzMessage(val) {
      console.info('Native_SmqzMessage===0==', val)
      if (val) {
        console.info('Native_SmqzMessage===1==', val)
        this.goCASmqz(val)
      }
    }
  },
  methods: {
    // 控制是否展示密码
    showEye() {
      this.eyesChange = !this.eyesChange
    },
    // 下载
    goScan() {
      // if (this.checkPIN(this.PIN)) {
      this.NativeScan()
      // }
    },
    async getScanData(token) {
      getSfxx.token = token
      this.asyncApiModule(getSfxx).then(res => {
        res = JSON.parse(res)
        this.zjmessage = res
        if (res !== null) {
          const ca = loadSearch('caListItem')[0]
          const ca1 = JSON.parse(ca)
          console.log('ca1==11===', ca1.serialNo)
          const pins = loadSearch('caListPin')[0]
          const pin = { 'CAXLH': ca1.serialNo,
            'newPin': pins,
            'kzzd': res.kzzd
          }
          console.log('pin=====', pin)
          if (process.env.NODE_ENV !== 'development') {
            try {
              this.Native_SmqzMessage = window.myapp.signMessage(JSON.stringify(pin))
            } catch (err) {
              console.log(err)
            }
            this.$bridge.callhandler('signMessage', res, (data) => {
              const datas = JSON.parse(data)
              console.log('goScanView------', data)
              this.Native_SmqzMessage = datas
            })
          } else {
            this.$toast('返回原生页面')
          }
        }
        // this.needIdCard = true
        // this.$toast.success('扫描成功')
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    },
    // 下载
    goSing(data) {

    },
    // 签名接口
    goCASmqz(value) {
      const ca = loadSearch('caListItem')[0]
      const ca1 = JSON.parse(ca)
      console.log('ca1==11===', ca1.serialNo)
      const pins = loadSearch('caListPin')[0]
      const cers = { 'cert': ca1.signCert, 'pwd': pins, 'result': value }
      const data = { 'value': cers, 'token': this.token }
      console.log('zjmessage=====', data)
      caSmqz.data = { xmxh: this.xmxh }
      this.asyncApiModule(caSmqz).then(res => {
        res = JSON.parse(res)
        console.log('zjmessage=====', res)
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    },

    showPopup() {
      this.show = true
    }
  }

}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
